'use client';
import React, { useState } from 'react';

const FeedbackHome = () => {
  const [rating, setRating] = useState(0);
  const [comment, setComment] = useState('');
  const [submitted, setSubmitted] = useState(false);

  const handleStarClick = (starIndex: number) => {
    setRating(starIndex + 1);
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setSubmitted(true);
  };

  return (
    <div className="container d-flex align-items-center justify-content-center min-vh-100">
      <div className="card shadow p-4 w-100" style={{ maxWidth: 420 }}>
        <div className="text-center mb-4">
          <span className="display-6 fw-bold text-warning">Feedback</span>
        </div>
        {submitted ? (
          <div className="alert alert-success text-center">Thank you for your feedback!</div>
        ) : (
          <form onSubmit={handleSubmit}>
            <div className="mb-4">
              <label className="form-label">Rating</label>
              <div className="d-flex justify-content-center">
                {[0, 1, 2, 3, 4].map((starIndex) => (
                  <button
                    key={starIndex}
                    type="button"
                    className="btn btn-link p-0 me-2"
                    onClick={() => handleStarClick(starIndex)}
                    style={{ fontSize: '2rem', color: starIndex < rating ? '#ffc107' : '#dee2e6' }}
                  >
                    ‚≠ê
                  </button>
                ))}
              </div>
              <div className="text-center mt-2">
                <small className="text-muted">
                  {rating === 0 && 'Click to rate'}
                  {rating === 1 && 'Poor'}
                  {rating === 2 && 'Fair'}
                  {rating === 3 && 'Good'}
                  {rating === 4 && 'Very Good'}
                  {rating === 5 && 'Excellent'}
                </small>
              </div>
            </div>
            <div className="mb-4">
              <label htmlFor="comment" className="form-label">Comment</label>
              <textarea
                className="form-control"
                id="comment"
                rows={4}
                value={comment}
                onChange={(e) => setComment(e.target.value)}
                placeholder="Share your thoughts..."
              />
            </div>
            <button
              type="submit"
              className="btn btn-warning w-100 rounded-pill fw-semibold py-2"
              disabled={rating === 0}
            >
              Submit Feedback
            </button>
          </form>
        )}
      </div>
    </div>
  );
};

export default FeedbackHome;
